import React, { Component } from 'react'
import { connect } from 'react-redux'
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import { IconButton, Tooltip } from '@material-ui/core';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import {translate} from "../../utils/translate"
import NotificationsIcon from '@material-ui/icons/Notifications';
export class actionsMenu extends Component {
        
    state={
            anchorEl:null
        }

        // to generate icon for an action
        getIcon = (action)=>{
            switch(action){
                case "UPDATE":
                    return "far fa-edit"
                case "CANCEL":
                    return "fas fa-ban"
                case "CLOSE":
                    return "fas fa-times-circle"
                case "DELETE":
                    return "fas fa-trash"
                case "REJECT":
                    return "far fa-thumbs-down"
                case "ACTIVATE":
                    return "fas fa-user-check"
                case "DEACTIVATE":
                    return "fas fa-user-times"
                default:
                    return "fas fa-user"
            }
        }


    getPermissionsToDoOnObject = (objectName,statusName)=>{
        // document_id props
        let id = this.props.document_id||null

        let actionsList=[]
        // to get the allowed permissions on object with specefic status from redux
        if(this.props.permissions&&this.props.permissions[objectName]&&this.props.permissions[objectName][statusName]){
            const permissionsList = [...this.props.permissions[objectName][statusName]];
             permissionsList.forEach(permission=>{
             actionsList.push(<MenuItem 
                     key={permission.label} 
                     onClick={()=>{
                        this.props.onActionClicked(permission.label,id);
                        this.handleClose()
                        }}>
                    <i 
                    style={{margin:"0px 5px"}} 
                    className={this.getIcon(permission.icon)}></i>{translate(permission.label)}
                   </MenuItem>)
             });

             // to check if there is actions i can do on the object with out status
             if(this.props.permissions[objectName]['NO-STATUS']){
                 let  permissionsList = this.props.permissions[objectName]['NO-STATUS'];
                permissionsList.forEach(permission=>{
                    actionsList.push(<MenuItem 
                            key={permission.label} 
                            onClick={()=>{
                               this.props.onActionClicked(permission.label,id);
                               this.handleClose();
                               }}>
                           <i 
                           style={{margin:"0px 5px"}} 
                           className={this.getIcon(permission.icon)}></i>{translate(permission.label)}
                          </MenuItem>)
                    });
             }
        };


        return actionsList;
        
    }


    renderMenuList = ()=>{
        if(this.props.object && this.props.status){
            return this.getPermissionsToDoOnObject(this.props.object,this.props.status)
        }
    }
    
    

    handleClick = event => {
        this.setState({anchorEl:event.currentTarget})
    };

    handleClose = () => {
        this.setState({anchorEl:null})
    };

    render() {
        const permissions = this.props.permissions && this.props.permissions['NOTIFICATION']
        let menuIcon = (<IconButton
                        aria-label="more"
                        aria-controls="long-menu"
                        aria-haspopup="true"
                        onClick={this.handleClick}>
                        <MoreVertIcon />
                        </IconButton>
        );

        // to check if the user has permission to create new NOTIFICATION
            let createIcon =null
            if (permissions && permissions['CREATE'] && (this.props.object==="DRIVER" || this.props.object==="RIDER")){
                createIcon =(
                    <Tooltip title={translate("CREATE_NOTIFICATION")}>
                    <IconButton
                    onClick={()=>this.props.onActionClicked("CREATE_NOTIFICATION")}>
                    <NotificationsIcon />
                    </IconButton>
                    </Tooltip>
                )
            }

        return (
            <div style={{display:"flex"}}>
                {this.props.status && this.props.object&&this.renderMenuList().length>0?menuIcon:null}
                {createIcon}
                <Menu
                    anchorEl={this.state.anchorEl}
                    keepMounted
                    open={Boolean(this.state.anchorEl)}
                    onClose={this.handleClose}
                >
                    {this.props.status && this.props.object&&this.renderMenuList()}
                </Menu>
            </div>
        )
    }
}

const mapStateToProps = (state) => ({
    permissions:state.permissions.permissions
})

const mapDispatchToProps = {
    
}

export default connect(mapStateToProps, mapDispatchToProps)(actionsMenu)
